import React, { useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { Box, Button, InputLabel, TextField, Typography } from '@mui/material'
import toast from "react-hot-toast";

const CreateBlog = () => {
    const id = localStorage.getItem('userId')
    const navigate = useNavigate()
    const [ inputs, setInputs ] = useState({
        title: '',
        description: '',
        image: ''
    })

    const handleChange = (e) => {
        setInputs(prevState => ({
            ...prevState,
            [e.target.name]: e.target.value
        }))
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            const {data} = await axios.post('/api/v1/blog/create-blog', {
                title: inputs.title,
                description: inputs.description,
                image: inputs.image,
                user: id
            })
            if (data?.success) {
                toast.success("Blog Created Successfully")
                navigate('/my-blogs')
            }
        } catch (error) {
            console.log(error)
        }
    }

    return (
        <>
            <form onSubmit={handleSubmit}>
                <Box width={'50%'}
                border={3}
                borderRadius={10}
                display={'flex'}
                flexDirection={'column'}
                padding={3}
                margin={'auto'}
                marginTop={'30px'}
                marginBottom={'20px'}
                boxShadow={'10px 10px 20px #ccc'}>
                    <Typography variant='h2'
                        textAlign={'center'}
                        fontWeight={'bold'}
                        padding={3}
                        color={'grey'}>
                            Create a Post
                    </Typography>
                    <InputLabel sx={{mb:1, mt:1, fontSize:"24px", fontWeight:"bold"}}>Title</InputLabel>
                    <TextField name='title' value={inputs.title} onChange={handleChange} margin='normal' variant='outlined' required/>
                    <InputLabel sx={{mb:1, mt:1, fontSize:"24px", fontWeight:"bold"}}>Description</InputLabel>
                    <TextField name='description' value={inputs.description} onChange={handleChange} margin='normal' variant='outlined' required/>
                    <InputLabel sx={{mb:1, mt:1, fontSize:"24px", fontWeight:"bold"}}>Image Url</InputLabel>
                    <TextField name='image' value={inputs.image} onChange={handleChange} margin='normal' variant='outlined' required/>
                    <Button sx={{mt:1, width:'150px', alignSelf:'center'}} type='submit' color='primary' variant='contained'>Submit</Button>
                </Box>
            </form>
        </>
    )
}

export default CreateBlog
